<template>
  <div>
      <span class="updated" v-if="matches.updated">MAJ:{{matches.updated.substring(11, 19)}}</span>
       
        <md-toolbar class="match_Live" v-if="match.statut == 'L'" v-for="match in matches.matches" :key="match.id" style="background-color:#F5F5F5;">
         <div style="width:100%;height:100%;" @click=toComment(match.id)>
              <img v-bind:src="getLogoClub(match.recevant.slug)" class="logo">
              <span class="score">{{match.recevant.score}}</span>
              <span class="time">{{match.time}}</span>
              <span class="score">{{match.visiteur.score}}</span>
              <img v-bind:src="getLogoClub(match.visiteur.slug)" class="logo">
              <div class="ligue">Ligue{{match.ligue}}</div> 
          </div>
        </md-toolbar> 
        <md-toolbar class="match" v-if="match.statut == 'P'" v-for="match in matches.matches" :key="match.id">
         <div style="width:100%;height:100%;" @click=toComment(match.id)>
              <img v-bind:src="getLogoClub(match.recevant.slug)" class="logo">
              <span class="temp">{{match.time}}</span>
              <img v-bind:src="getLogoClub(match.visiteur.slug)" class="logo">
              <div class="ligue">Ligue{{match.ligue}}</div>     
          </div>
        </md-toolbar> 

        <md-toolbar class="match" v-if="match.statut == 'J'" v-for="match in matches.matches" :key="match.id">
         <div style="width:100%;height:100%;" @click=toComment(match.id)>
              <img v-bind:src="getLogoClub(match.recevant.slug)" class="logo">
              <span class="score">{{match.recevant.score}}</span>
              <span class="time">-</span>
              <span class="score">{{match.visiteur.score}}</span>
              <img v-bind:src="getLogoClub(match.visiteur.slug)" class="logo">
              <div class="ligue">Ligue{{match.ligue}}</div>     
          </div>
        </md-toolbar>   
  </div>
</template>

<script>
var debounce = require('lodash.debounce')

export default {
  name: 'matchs',

  data () {
    return {
      matches: [],
      match: '',
      id: ''
    }
  },

  methods: {
    toComment (id) {
      this.$localStorage.set('id', id)
      this.$router.push('Commentaires')
    },
    getLogoClub (value) {
      return 'static/img/clubs/' + value + '.svg'
    },
    getTime (match) {
      if (match.status === 'L') return match.time + '\''
      else if (match.status === 'P') return 'P'
      else if (match.status === 'J') return 'J'
    },
    update () {
      this.matches = this.$store.state.matches
    },
    updateMatches: debounce(function () {
      this.update()
    }, 1000)
  },

  watch: {
    matches: function () {
      this.updateMatches()
    }
  },

  created: function () {
    window.addEventListener('online', this.updateMatches)
  },

  mounted: function () {
    console.log('match mounted')
    this.update()
  }
}
</script>

<style>

  html, body {
    width:100%;
    height: 100%;
    margin:0;
    padding:0;
  }
  .updated{
    float: right;
    position: relative;
    bottom: 20px;
  }
  .match {
    padding:0;
  }
  .match_Live {
    padding:0;
    background-color: #F5F5F5;
  }
  .score{
    margin: 5px auto;
    font-weight: bold;
    font-size: calc(25px + 0.6vw + 0.4vh); 
    padding: 5px 0;
    padding:0 30px;
    vertical-align:middle;
  }
  .time{
    margin: 5px auto;
    font-weight: bold;
    font-size: calc(12px + 0.6vw + 0.4vh); 
    padding: 5px 0;
    text-align: center;
  }
  .temp{
    margin: 5px auto;
    font-weight: bold;
    font-size: calc(15px + 0.6vw + 0.4vh); 
    padding: 5px 66px;
    text-align: center;
  }
  .logo{
    position: relative;
    top: 5px;
    height: calc(40px + 6vw);
    width: calc(40px + 6vw);
    padding: 0;
    max-width: 80px;
    max-height: 80px;
  }
  .ligue{
    position: relative;
    bottom: 20px;
    font-size: calc(9px + 0.6vw);
  }

  .classement{
    font-size: calc(5px + 1vw);
  }
  .carton{
    font-size: calc(7px + 1vw);
  }
  
</style>



