<template>
	<div>
		<span class="updated" v-if="commentaires.updated">MAJ:{{commentaires.updated.substring(11, 19)}}</span>
    	
    <md-toolbar class="match" v-if="commentaires.match.statut == 'P'" :key="commentaires.match.id">
      <div style="width:100%">
          <div class="nom" style=" float:left;width:40%; display:table-cell;">
            <span class="classement">({{commentaires.match.recevant.classement}})</span>&nbsp{{commentaires.match.recevant.club}}
          </div>
          <div class="temp_commentaires" style=" float: left; width:20%; display:table-cell;">{{commentaires.match.time}}</div>
          <div class="nom" style=" float:left; width:40%; display:table-cell;">{{commentaires.match.visiteur.club}}&nbsp
            <span class="classement">({{commentaires.match.visiteur.classement}})</span>
          </div>
          <div class="ligue_commentaires" style="width:100%;">Ligue{{commentaires.match.ligue}}</div>
      </div>
    </md-toolbar>

    <md-toolbar class="match" v-if="commentaires.match.statut == 'J' || commentaires.match.statut == 'L'" :key="commentaires.match.id">
      <div class="nom" style=" float:left;width:35%; display:table-cell;">
        <span class="classement">({{commentaires.match.recevant.classement}})</span>
        &nbsp{{commentaires.match.recevant.club}}
      </div>
      <div style=" float:left; width:30%; display:table-cell; margin:auto;">
        <span class="score_commentaires">{{commentaires.match.recevant.score}}</span>
        <span class="time">-</span>
        <span class="score_commentaires">{{commentaires.match.visiteur.score}}</span>
      </div>
      <div class="nom" style=" float:left; width:35%; display:table-cell;">{{commentaires.match.visiteur.club}}&nbsp
        <span class="classement">({{commentaires.match.visiteur.classement}})</span>
      </div>
      <div style="width:100%">
        <div style="width:40%; float:left;">
          <img v-for="i in parseInt(commentaires.match.recevant.nbRed)" src="static/img/commentairesIcons/rouge.png" style="width:15px;height:15px;"> 
          <img v-for="i in parseInt(commentaires.match.recevant.nbYellow)" src="static/img/commentairesIcons/jaune.png" style="width:15px;height:15px;">
        </div>
        <div class="ligue_commentaires" style="width:20%; float:left;">&nbsp Ligue{{commentaires.match.ligue}}&nbsp</div>
        <div style="width:40%; float:left;">
          <img v-for="i in parseInt(commentaires.match.visiteur.nbRed)" src="static/img/commentairesIcons/rouge.png" style="width:15px;height:15px;"> 
          <img v-for="i in parseInt(commentaires.match.visiteur.nbYellow)" src="static/img/commentairesIcons/jaune.png" style="width:15px;height:15px;">
        </div> 
      </div>
    </md-toolbar>

		<div>
			<table style="border:none;cellspacing:0;">
				<tbody>
					<tr v-for="commentaire in commentaires.comments" style="height:80px;width:100%;">
						<td v-if="commentaire.type === 'But'" width="2%" style="background-color:rgb(102, 153, 115)"></td>
						<td v-else width="2%" style="background-color: rgb(194, 194, 214)"></td>
						<td width="18%" style="background-color:rgb(224, 224, 235);position:relative;">
							<div class="minute" v-if="commentaire.minute && (commentaire.type ==='But' || commentaire.type ==='CRg' || commentaire.type ==='CJn' || commentaire.type ==='Rpl' || commentaire.type ==='Pen' || commentaire.type ==='Crn' || commentaire.type ==='CpF')">{{commentaire.minute}}'</div>
							<div class="minute_middle" v-if="commentaire.minute && commentaire.type !=='But'  && commentaire.type !=='CRg' && commentaire.type !=='CJn' && commentaire.type !=='Rpl' && commentaire.type !=='Pen' && commentaire.type !=='Crn' && commentaire.type !=='CpF'">{{commentaire.minute}}</div>
							<div class="type">
								<img v-if="commentaire.type === 'But'"src="static/img/commentairesIcons/ball.png" style="width:20px;height:20px;">
								<img v-if="commentaire.type === 'CRg'"src="static/img/commentairesIcons/rouge.png" style="width:20px;height:20px;">
								<img v-if="commentaire.type === 'CJn'"src="static/img/commentairesIcons/jaune.png" style="width:20px;height:20px;">
								<img v-if="commentaire.type === 'Rpl'"src="static/img/commentairesIcons/change.png" style="width:20px;height:20px;">
								<img v-if="commentaire.type === 'Pen'"src="static/img/commentairesIcons/penalty.png" style="width:20px;height:20px;">
								<img v-if="commentaire.type === 'Crn'"src="static/img/commentairesIcons/corner.png" style="width:20px;height:20px;">
								<img v-if="commentaire.type === 'CpF'"src="static/img/commentairesIcons/coup_franc.png" style="width:20px;height:20px;">
							</div>
						</td>
						<td width="80%">
							<div class="text" v-if="commentaire.type ==='But' || commentaire.type === 'Pen'"style="font-weight:bold;text-align:left;">{{commentaire.text}}</div>
							<div class="text" v-else style="font-weight:normal;text-align:left;">{{commentaire.text}}</div>
						</td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</template>

<script>
var debounce = require('lodash.debounce')

export default {
  name: 'commentaires',

  data () {
    return {
      commentaires: '',
      commentaire: ''
    }
  },
  methods: {
    updateCommentaires: debounce(function () {
      if (this.$localStorage.get('id')) {
        this.$http.get('https://qlf-www-lfp-fr.aw.atos.net/ws/comments.json?id=' + this.$localStorage.get('id')).then(response => {
        // json success call back
          this.commentaires = response.data
          this.$localStorage.set('commentaires', JSON.stringify(response.data))
        }, response => {
          // error callback
          console.log('error call data')
        })
      } else {
        this.commentaires = JSON.parse(this.$localStorage.get('commentaires'))
      }
    }, 1000)
  },

  watch: {
    matches: function () {
      this.updateCommentaires()
    }
  },

  created: function () {
    this.updateCommentaires()
    window.addEventListener('online', this.updateCommentaires)
  }
}
</script>

<style>
body,html {
  margin: 0;
  width: 100%;
  height: 100%;
}
.updated{
  float: right;
  position: relative;
  bottom: 20px;
}
.score_commentaires{
	margin: 5px auto;
	font-weight: bold;
	font-size: calc(22px + 0.6vw + 0.4vh); 
	padding: 5px 0;
	padding:0 10px;
	vertical-align:middle;
}
.classement{
   font-size: calc(8px + 0.7vw);
}
.nom{
	font-weight: bold;
  font-size: calc(10px + 0.7vw);	
}
.ligue_commentaires{
	margin: auto;
}
.temp_commentaires{
	margin: auto;
	font-weight: bold;
	font-size: calc(12px + 0.6vw + 0.4vh); 
}
.commentaires{
	min-height: 80px;
}
.commentaire{
	display: flex;
	min-height: 100px;
	width: 100%;
	margin: 10px 0;
}
.left-border{
	background-color: rgb(194, 194, 214);
}
.minute{
	font-weight: bold;
	font-size: 20px;

	position: absolute; 
	width:100%;
	top:0;
	left:0;
	text-align: center;
}
.minute_middle{
	font-weight: bold;
	font-size: 20px;

	position: absolute; 
	width:100%;
	top:40%;
	left:0;
	text-align: center;
}
.type{
	position: absolute;
	width:100%;
	top:30px;
	left:0;
	text-align: center;
}
.right{
	min-height: 80px;
	border-bottom: 1px dashed rgb(194, 194, 214);
}
.text{
	display: flex;
	padding: 10px 0;
	line-height: 120%;
	font-size: calc(12px + 0.6vw + 0.4vh);
}
</style>


