<template>
  <div>
    <span class="updated" v-if="classements.updated">MAJ:{{classements.updated.substring(11, 19)}}</span>
    <br>
    <div class="classements" style="width:100%;">
      <div class="classement-item" v-for="classement in classement1s">
        <div style="width:5%;display:table-cell;">
          {{classement.position}}
        </div>
        <div style="width:5%;display:table-cell;">
          <img v-bind:src="getLogoClub(classement.slug)" class="logo">
        </div>
        <div style="width:30%;display:table-cell;">
          {{classement.club}}
        </div>
        <div style="width:10%;display:table-cell;font-size: calc(12px + 0.6vw + 0.4vh);">
          {{classement.points}}
        </div>
        <div style="width:10%;display:table-cell;">
          {{classement.joues}}
        </div>
        <div style="width:10%;display:table-cell;">
          {{classement.diff}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
var debounce = require('lodash.debounce')
export default {
  name: 'classements',

  data () {
    return {
      classements: '',
      classement1s: [],
      classement2s: [],
      classement: ''
    }
  },
  methods: {
    getLogoClub (value) {
      return 'static/img/clubs/' + value + '.svg'
    },
    update () {
      this.classements = this.$store.state.classements
      this.classement1s = this.classements.ligue1.classement
      this.classement2s = this.classements.ligue2.classement
    },
    updateClassements: debounce(function () {
      this.update()
    }, 1000)
  },

  watch: {
    classements: function () {
      this.updateClassements()
    }
  },

  created: function () {
    window.addEventListener('online', this.updateClassements)
  },

  mounted: function () {
    this.update()
  }
}
</script>

<style>
.classements{
  font-size: calc(10px + 0.6vw + 0.4vh);
  font-weight: 600;
}
.classement-head{
  text-align: center;
  vertical-align: middle;
  min-height: 10px;
}
.classement-item{
  /*border-bottom: 1px solid;*/
  text-align: center;
  vertical-align: middle;
  min-height: 30px;
}
.updated{
  float: right;
  position: relative;
  bottom: 20px;
}
.logo{
  height: calc(40px + 6vw);
  width: calc(40px + 6vw);
  padding: 0;
  max-width: 80px;
  max-height: 80px;
}
</style>



